.wrapper{
  display: grid;
  grid-template-rows: 70px 1fr;
  grid-template-columns: 280px 1fr;
  grid-template-areas:
  "h h"
  "m c"
;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.0001) 0%, rgba(255, 255, 255, 0.1) 100%);
}

.headerStatic{
  z-index: 3;
  background-color: #2B2C3A;
  position: sticky;
  top: 0;
  grid-area: h;
  padding-right: 45px;
  height: 70px;
  border-bottom: 1px solid rgba(255, 255, 255, .1);
}
.navBar{

  grid-area: m;
  width: 100%;
  min-height: 100%;
  height: 100%;
  max-width: 280px;
  background: rgba(156, 165, 236, 0.04);
}
.logo{
  margin-top: 19px;
  margin-left: 30px;
}
.logoBlock{
  float: left;
  display: flex;
  width: 280px;
  height: 70px;
  background: rgba(156, 165, 236, 0.04);
}

.nav {

  position: sticky;
  top: 70px;
  list-style-type: none;
  margin: 0;
  width: 100%;
  overflow: hidden;
  padding: 23px 0 23px 0;
  border-bottom: 1px solid rgba(255, 255, 255, .1);
}

.nav-item {
  width: 280px;
  display: table;
  border-bottom: 1px solid rgba(63, 46, 73, 0.3);
}

.nav-item:last-child {
  border-bottom: none;
}

.nav-link {
  width: 250px;
  padding: 7px 15px 7px 30px;
  font-weight: 400;
  font-size: 14px;
  text-align: left;
  line-height: 20px;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-decoration: none;
  display: table;
  color: #fff;
  font-style: normal;
}
.nav-submenu-link a{
  width: 100%;
  height: 40px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 10px;
  display: table;
  text-decoration: none;
  color: #fff;
}

.nav-submenu {
  overflow: hidden;
  max-height: 0;
  transition: max-height .5s;
}
.nav-submenu:target {
  max-height: 20rem;
}
.nav-submenu-link {
  width: 100%;
  display: table;
  height: 40px;
  font-size: 16px;
  background: transparent;
  transition: background 0.3s ease-in-out;
}
.nav-submenu-link span{
  vertical-align: middle;
  display: table-cell;
  margin-bottom: 15px;
}
.nav-submenu-link:hover {
  background-color: rgba(255,255,255,0.03);
  color: #fff;
  border-left: 3px solid #6f6eff;
}
.nav-submenu-link-frst:hover{

  background-color: rgba(255,255,255,0.03);
  color: #fff;
}
.nav-submenu-link:hover .svgResize svg path{
  fill: #6f6eff;
}
.svgResize{
  display: table-cell;
  padding: 11px 24px 11px 50px;
  height: 18px;
  width: 18px;
}
.svgResize svg{
  display: block;
  margin: auto;
}
.svgMain svg{
  display: block;
  height: 6px;
  width: 10px;
  margin: auto;
  transform: rotate(-90deg);
}
.svgMain{
  vertical-align: middle;
  height: 20px;
  display: table-cell;
  width: 20px;
}
.mailSender{
  z-index: 1001;
  position: fixed;
  bottom: 0;
  display: table;
}
.mailSender span {
  vertical-align: top;
  display: table-cell;
  padding-top: 5px;
  padding-right: 60px;
  padding-left: 30px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 10px;
  opacity: .5;
  color: #fff;
}

.toggleBtn{
  margin-bottom: 50%;

}

*, *:after, *:before {
  box-sizing: border-box;
}
.toggleBtn input[type="checkbox"] {
  visibility: hidden;
}
.toggleBtn input[type="checkbox"]:checked + label {
  background-color: transparent;
}
.toggleBtn input[type="checkbox"]:checked + label:before {
  transform: translateX(20px);
  background-color: #6f6eff;
}
.toggleBtn label {
  display: flex;
  width: 42px;
  height: 22px;
  border: 1px solid rgba(255,255,255, .2);
  border-radius: 15px;
  position: relative;
  transition: transform 0.35s ease-in-out;
  transform-origin: 50% 50%;
  cursor: pointer;
}
.toggleBtn label:before {
  transition: transform 0.35s ease-in-out;
  content: "";
  display: block;
  position: absolute;
  width: 16px;
  height: 16px;
  background-color: #fff;
  border-radius: 50%;
  opacity: .5;
  top: 2px;
  left: 2px;
}
.subMenu{
  position: relative;
  float: left;
  top: 24px;
  width: 298px;
  height: 24px;
  display: flex;
  border-right: 1px solid rgba(255, 255, 255, .1);
}
.subMenu a {
  padding-top: 7px;
  height: 24px;
  opacity: 0.6;
  color: white;
  text-decoration: none;
  display: inline-block;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 10px;
  transition: all 200ms;
}
.subMenu a:nth-child(1){
  margin-left: 55px;
  margin-right: 28px;
}
.subMenu a:nth-child(3){
  margin-left: 23px;
  margin-right: 28px;

}

.subMenu a:hover{
  border-bottom: 1px solid rgba(255, 255, 255, .75);
  color: rgba(255,255,255, .75);
}

.inline {
  display: inline-block;
  margin-right: 10px;
}
.test{
  margin-top: 24px;
  height: 24px;
  border-radius: 5px;
  padding-left: 9px;
  padding-right: 9px;
  background-color:rgba(255,255,255, 0.06);
}
.test span{
  color: #fff;
}
.rightBar{
  float: right;
  position: relative;
  height: 70px;
  display: flex;
}
.rightBarSecond{
  float: right;
  position: relative;

}
.normalText{
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 10px;
}
.bold{
  display: table-cell;
  font-style: normal;
  font-weight: 600;
  font-size: 13px;
  vertical-align: middle;
  line-height: 10px;
  padding-right: 4px;
}
.tiny{
  display: table-cell;
