}
.avatar{
  margin-top: 14px;
  width: 44px;
  height: 44px;
  background-image: url("img.png");
  background-size: contain;
}
.info{
  margin-top: 22px;
  color: #fff;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 10px;
}
.info-pro {
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 10px;
  padding-top: 2px;
  display: inline-block;
  width: 40px;
