  font-size: 22px;
  line-height: 18px;
  color: #FFFFFF;
}
.btn{
  display: flex;
  justify-content: flex-end;
  align-items: center;
  grid-area: lect;
}
.allLessons{
  width: 86px;
  height: 30px;
  background: linear-gradient(90deg, #6B69F9 0%, #6F6EFF 100%);
  border-radius: 4px;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
