.navigation__checkbox {
  display: none;
}

.navigation__button {
  position: fixed;
  top: 22px;
  right: 30px;
  height: 25px;
  width: 25px;
  text-align: center;
  border-radius: 50%;
  z-index: 300;
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

@media screen  and (min-width: 670px) {
  .navigation__button {
    top: 19px;
    right: 30px;
    height: 30px;
    width: 30px;
  }
}

.navigation__background {
  position: fixed;
  top: 19px;
  right: 30px;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background: #2B2C3A;
  background-size: cover;
  background-position: center;
  z-index: 100;
  transition: all 400ms cubic-bezier(0.86, 0, 0.07, 1);
}


.navigation__nav {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  opacity: 0;
  width: 0;
  visibility: hidden;
  z-index: 200;
  transition: all 800ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.navigation__list {
  /*@include center;*/
  position: absolute;
  top: 50%;
  left: 45%;
  padding-right: 30px;
  transform: translate(-50%, -50%);
  text-align: center;
  width: 100%;
  list-style: none;
}
.navigation__item{
  display: block;
}
.spaceSpan{
  color: #fff;
  margin-left: 30px;
}
.spaceSpan span{
  margin-right: 15px;
}

.navigation__button i{
  font-size: 25px;
  color: #fff;
}

.navigation__link:link,
.navigation__link:visited {
  display: inline-block;
  padding: 1rem 2rem;
  text-transform: uppercase;
  color: #f4f4f4;
  font-size: 2.4rem;
  text-decoration: none;
  transition: all .2s;
}

@media(min-width: 768px) {
  .navigation__link:link,
  .navigation__link:visited {
    font-size: 3.8rem;
  }
}

.navigation__link span {
  margin-right: 1.5rem;
  display: inline-block;
}

.navigation__link:hover {
  color: #C1D0D0;
  transform: scale(1.1);
}

.navigation__checkbox:checked~.navigation__background {
  transform: scale(100)
}
.navigation__checkbox:checked~.fa-bars{
  display: none;
}

.navigation__checkbox:checked~.navigation__nav {
  width: 100%;
  visibility: visible;
  opacity: 1;
}

.navigation__icon {

  position: relative;
}



.navigation__icon,
.navigation__icon::before,
.navigation__icon::after {
  display: inline-block;
  width: 30px;
  height: 2px;
  background-color: #4A676A;
}

@media (min-width: 768px) {
  .navigation__icon,
  .navigation__icon::before,
  .navigation__icon::after {
    width: 30px;
  }
}

.navigation__icon::before,
.navigation__icon::after {
  content: '';
  position: absolute;
  left: 0;
  transition: all 200ms;
}

.navigation__icon::before {
  top: -.8rem;
}

.navigation__icon::after {
  top: .8rem;
}

.navigation__button:hover .navigation__icon::before {
  top: -1rem;
}

.navigation__button:hover .navigation__icon::after {
  top: 1rem;
}

.navigation__checkbox:checked+.navigation__button .navigation__icon {
  background-color: transparent;
}

.navigation__checkbox:checked+.navigation__button .navigation__icon::before {
  top: 0;
  transform: rotate(135deg);
}

.navigation__checkbox:checked+.navigation__button .navigation__icon::after {
  top: 0;
  transform: rotate(-135deg);
}

